import React, { useState, useEffect } from 'react';
import Modal from '../../components/UI/Modal/Modal';
import { useTranslation } from 'react-i18next';

import LogIn from '../../components/Auth/LogIn';
import LogUp from '../../components/Auth/logUp';
import RestorePassword from '../../components/Auth/RestorePassword/RestorePassword';

const MobileLogIn = ( props ) => {

    const { t } = useTranslation();

    const [authMode, setAuthMode ] = useState('log-in');
    const [ emailSuccessModal, setEmailSuccessModal ] = useState(false);

    const changeAuthModeToLogIn = () => {
        setAuthMode('log-in');
    };

    const changeAuthModeToLogUp = () => {
        setAuthMode('log-up');
    }

    const changeModeToRestore = () => {
        setAuthMode('restore-pass');
    }

    const onCloseEmailModalHandler = () => {
        setEmailSuccessModal(false);
        props.history.push(props.match.path.slice(0, 3) + '/log-in');
    }

    useEffect(() => {
        if ( props.location.search === '?signup=true' ) {
            setEmailSuccessModal(true);
        }
    }, []);


    return (
        <div style={{paddingTop: '50px'}}>
            {authMode === 'log-in' && 
            (
                <LogIn 
                {...props}
                switchMode={changeAuthModeToLogUp} 
                restoreMode={changeModeToRestore}/>
            )}
            {authMode === 'log-up' && 
            (
                <LogUp
                {...props} 
                switchMode={changeAuthModeToLogIn} />
            )}
            {authMode === 'restore-pass' && 
            (
                <RestorePassword
                {...props} 
                switchMode={changeAuthModeToLogIn} />
            )}
            {/* modal for succesfull log up  */}
            <Modal
            show={emailSuccessModal}
            modalClosed={onCloseEmailModalHandler}>
                <p style={{
                    color: 'green',
                    textAlign: 'center',
                    fontWeight: '700',
                    fontSize: '24px'
                }}>{t('order.personalData.successfully')}</p>
            </Modal>
        </div>
    );
};

export default MobileLogIn;